html {
  background: rgb(248, 248, 154);
}

a {
  text-decoration: none;
  font-size: 24px;
  color: white;
  margin-top: 2vh;
}

icon {
  font-size: 24px;
  margin-top: 2vh;
  color: white;
}

form {
  display: flex;
  flex-direction: column;
  width: 20vw;
  gap: 20px;
  margin: 20vh auto;
  background: rgb(245, 166, 178);
  padding: 20px;
  border-radius: 20px;
}

input {
  background: rgb(240, 240, 196);
  border: none;
  height: 20px;
  padding: 10px;
  border-radius: 10px;
}

a:hover, icon:hover {
  color: rgb(78, 29, 29);
  opacity: 0.7;
  scale: 1.1;
}

.navbar-div {
  display: flex;
  justify-content: space-around;
  background: rgb(245, 166, 178);
  justify-items: center;
  height: 8vh;
  position: fixed;
  width: 100vw;
  margin-top: -20vh;
}

.submit {
  padding: 2px;
  width: 10vw;
  align-self: center;
  background: rgb(137, 240, 137);
  border: 1px solid black;
}